export default {
	data() {
		return {
			details: {}
		};
	},
	onLoad: function(option) {
		this.loadData(option.id);
	},
	methods: {
		loadData(id) {
			this.$minApi
				.goodsDetails({ id: id })
				.then(res => {
					this.details = res.data;
				})
				.catch(err => {
					console.log(err);
				});
		},
		toPay(id) {
			uni.navigateTo({
				url: '/pages/order/ready?goods=' + id
			});
		}
	}
};
